import * as React from 'react';
import { ZhiPlugin } from '@ali/4ever-cangjie';

import Mention from '../components/mentionMobile';
import { MentionPluginConfig } from '../types';

export default (mentionId, mentionConfig: MentionPluginConfig): ZhiPlugin['renderNode'] => {
  return {
    mention: (props) => {
      return (
        <Mention
          {...props as any}
          mentionId={mentionId}
          mentionConfig={mentionConfig}
        />
      );
    },
  };
};
